@import-normalize;

html,
body {
  height: 100%;
}

body {
  margin: 0;

  color: var(--accent-color);

  background-color: var(--primary-dark-color);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  width: 8px;
}

body::-webkit-scrollbar-track {
  border-radius: var(--border-radius);
  background-color: var(--bg-button-color-opacity);
}

body::-webkit-scrollbar-thumb {
  border-radius: var(--border-radius);
  background-color: var(--bg-button-color);
}

body::-webkit-scrollbar-thumb:hover {
  background-color: var(--accent-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;

  background-color: var(--bg-button-color);
  border: none;

  transition: background-color var(--transition-duration) var(--transition-timing-function);
  cursor: pointer;

  outline: none;
}

button:hover,
button:focus {
  background-color: var(--accent-color);
}

a {
  list-style: none;
  text-decoration: none;

  color: var(--bg-button-color);

  transition: color var(--transition-duration) var(--transition-timing-function),
    transform var(--transition-duration) var(--transition-timing-function);
}

a:hover,
a:focus {
  color: var(--accent-color);
  transform: scale(1.1);
}

#root {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  /* height: 100%; */
}

main {
  flex-grow: 1;
  /* max-height: calc(100vh - (var(--header-height) + var(--footer-height))); */
}

.title {
  margin-bottom: 30px;

  font-weight: 500;
  font-size: 48px;
  text-align: center;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.active {
  color: var(--accent-color);
  transform: scale(1.1);
}

.loader {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.modalOpen {
  animation: modalA var(--transition-duration) var(--transition-timing-function);
}

.modalClose {
  animation: modalB var(--transition-duration) var(--transition-timing-function);
}

.modalContentOpen {
  animation: modalContentA var(--transition-duration) var(--transition-timing-function);
}

.modalContentClose {
  animation: modalContentB var(--transition-duration) var(--transition-timing-function);
}

@keyframes modalA {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes modalB {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes modalContentA {
  0% {
    transform: translateX(-1500px) skewX(30deg) scaleX(1.3);
  }
  70% {
    transform: translateX(30px) skewX(0deg) scaleX(0.9);
  }
  100% {
    transform: translateX(0px) skewX(0deg) scaleX(1);
  }
}

@keyframes modalContentB {
  0% {
    transform: translateX(0px) skewX(0deg) scaleX(1);
  }
  30% {
    transform: translateX(-30px) skewX(-5deg) scaleX(0.9);
  }
  100% {
    transform: translateX(1500px) skewX(30deg) scaleX(1.3);
  }
}
